import { RouterModule } from '@angular/router';

 import { PageComponent } from "./page/page.component";

import { EraserComponent } from "./eraser/eraser.component";
import { CropcloneComponent } from "./cropclone/cropclone.component";

import { PhotomontageComponent } from "./photomontage/photomontage.component";
const routes = [
  { path: "", redirectTo: "home", pathMatch: "full" },
  { path: "home", component: PageComponent, data: { state: "home" } },
  { path: "eraser", component: EraserComponent, data: { state: "about" } },
  {
    path: "photomontage",
    component: PhotomontageComponent,
    data: { state: "photomontage" }
  },
  {
    path: "cropclone",
    component: CropcloneComponent,
    data: { state: "cropclone" }
  },
  { path: "**", component: PageComponent }
];

export const AppRouting = RouterModule.forRoot(routes, {
  useHash: true
});
